import React, { useState, useEffect } from 'react'
import {
    Form, Input, Button, Select, Card, Row,
    Modal, Divider, Space, Upload, Table
} from 'antd';
import Api from '../../../helpers/api';
import { message } from "antd";

import {
    ReloadOutlined,
    ArrowLeftOutlined,
    SaveOutlined,
    LoadingOutlined,
    PlusOutlined
} from '@ant-design/icons';
import handleApiError from '../../../helpers/handleApiError';

const { Option } = Select;
const api = new Api();

const FormPage = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [form] = Form.useForm();
    const [role, setRole] = useState([])
    const [status, setStatus] = useState([
        {
            key: 0,
            name: 'Pending'
        },
        {
            key: 1,
            name: 'Active'
        },
        {
            key: 3,
            name: 'Suspend'
        },
        {
            key: 4,
            name: 'Blocked'
        },
    ])
    const [fileList, setFileList] = useState([])
    const [previewImage, setPreviewImage] = useState('')
    const [showPreviewImage, setShowPreviewImage] = useState(false)

    useEffect(() => {
        const { add, edit } = props

        if (edit) {
            getEditData()
        }

        fetchRole()
    }, []);

    const onFinish = (data) => {
        setLoading(true)

        let apiPost
        //handling if form add
        if (props.add) {
            apiPost = api.storeUser(data)
        } else if (props.edit) {
            //handling if form edit
            const { id } = props.match.params
            apiPost = api.updateUser(id, data)
        }

        apiPost.then(response => {
            console.log(response)
            setLoading(false)
            // console.log(response.data);
            message.success('save data success');
            props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
                // message.error(message);
            }
            handleApiError(error, props)
        });
    };

    const getEditData = () => {
        const { id } = props.match.params

        api.viewUser(id)
            .then(response => {
                const { username, firstname, lastname, email, mobile, roles_id, status, detail } = response.data
                form.setFieldsValue({
                    username,
                    firstname,
                    lastname,
                    email,
                    nik: detail ? detail.nik : '',
                    position: detail ? detail.position : '',
                    mobile,
                    address: detail ? detail.address : '',
                    role_id: roles_id,
                    status: status,
                });

                const file = {
                    uid: '-1',
                    name: 'image',
                    status: 'done',
                    url: detail ? detail.photo_url_full : '',
                }

                setPreviewImage(detail ? detail.photo_url_full : '')
                setFileList([file])
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const handleReset = () => {
        form.setFieldsValue({
            password: '',
            confirmation_password: '',
            firstname: '',
            lastname: '',
            nik: '',
            position: '',
            mobile: '',
            address: '',
            role_id: [],
        });

        if (props.add) {
            form.setFieldsValue({
                username: '',
                email: '',
            });
        }
    }

    const handleRoleSearch = (value) => {
        fetchRole(value)
    }

    const handleRoleChange = () => {
        fetchRole('')
    }

    const fetchRole = (q) => {
        api.getMasterRole({ q: q }).then(response => {
            const { data } = response.data;
            setRole(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const handleUpload = (file) => {
        setLoading(true);

        let id
        if (props.add) {
            id = 0
        } else {
            id = props.match.params.id
        }

        api.updatePhotoProfileUser(id, { image: file })
            .then((response) => {
                console.log(response.data);
                message.success('change photo success');
                setLoading(false);
            })
            .catch(error => {
                console.log(error.response);
                const { errors } = error.response.data
                setLoading(false)
                setErrors(errors)
                // handleApiError(error, props)
            })
    }

    const handleDeleteUpload = () => {
        setLoading(true);

        let id
        if (props.add) {
            const { user } = props
            id = user ? user.id_hash : null
        } else {
            id = props.match.params.id
        }

        api.deletePhotoProfileUser(id)
            .then((response) => {
                console.log(response.data);
                message.success('change photo success');
                setLoading(false);
                setFileList([])
            })
            .catch(error => {
                console.log(error.response);
                const { errors } = error.response.data
                setLoading(false)
                setErrors(errors)
                handleApiError(error, props)
            })
    }

    const uploadButton = (
        <div>
            {loading ? <LoadingOutlined /> : <PlusOutlined />}
            <div className="ant-upload-text">Upload</div>
        </div>
    );

    const { add, edit } = props
    const inputDisabled = add

    return (

        <Card>
            <Form name="complex-form" onFinish={onFinish} labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}
                form={form}>

                <Form.Item name="username" label="Username"
                    validateStatus={errors && errors.username ? 'error' : undefined}
                    help={errors ? errors.username : null}
                    rules={[{ required: true, message: 'Please input username' }]}>
                    <Input disabled={!inputDisabled} />
                </Form.Item>

                {/* <Form.Item name="password" label="Password"
                    validateStatus={errors && errors.password ? 'error' : undefined}
                    help={errors ? errors.password : null}
                    rules={[{ required: inputDisabled, message: 'Please input password' }]}>
                    <Input.Password />
                </Form.Item>

                <Form.Item name="confirmation_password" label="Password Confirmation"
                    validateStatus={errors && errors.confirmation_password ? 'error' : undefined}
                    help={errors ? errors.confirmation_password : null}
                    rules={[{ required: inputDisabled, message: 'Please input confirmation password' }]}>
                    <Input.Password />
                </Form.Item> */}

                <Form.Item name="firstname" label="First Name"
                    validateStatus={errors && errors.firstname ? 'error' : undefined}
                    help={errors ? errors.firstname : null}
                    rules={[{ required: true, message: 'Please input first name' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="lastname" label="Last Name"
                    validateStatus={errors && errors.lastname ? 'error' : undefined}
                    help={errors ? errors.lastname : null}
                    rules={[{ required: true, message: 'Please input last name' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="email" label="Email"
                    validateStatus={errors && errors.email ? 'error' : undefined}
                    help={errors ? errors.email : null}
                    rules={[{ required: inputDisabled, message: 'Please input email' }]}>
                    <Input disabled={!inputDisabled} />
                </Form.Item>

                <Form.Item name="nik" label="NIK"
                    validateStatus={errors && errors.nik ? 'error' : undefined}
                    help={errors ? errors.nik : null}
                    rules={[{ required: false, message: 'Please input nik' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="position" label="Position"
                    validateStatus={errors && errors.position ? 'error' : undefined}
                    help={errors ? errors.position : null}
                    rules={[{ required: true, message: 'Please input position' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="mobile" label="Phone Number"
                    validateStatus={errors && errors.mobile ? 'error' : undefined}
                    help={errors ? errors.mobile : null}
                    rules={[{ required: true, message: 'Please input phone' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="address" label="Address"
                    validateStatus={errors && errors.address ? 'error' : undefined}
                    help={errors ? errors.address : null}
                    rules={[{ required: true, message: 'Please input address' }]}>
                    <Input.TextArea />
                </Form.Item>

                <Form.Item name="role_id" label="Role"
                    validateStatus={errors && errors.role_id ? 'error' : undefined}
                    help={errors ? errors.role_id : null}
                    rules={[{ required: true, message: 'Please select role' }]}>
                    <Select
                        mode="multiple"
                        placeholder="Select"
                        filterOption={false}
                        // showSearch
                        onChange={handleRoleChange}
                        onSearch={handleRoleSearch}
                        name="role_id">

                        {role.map(item => (
                            <Option key={item.id} value={item.id}>{item.name}</Option>
                        ))}
                    </Select>
                </Form.Item>

                <Form.Item name="status" label="Status"
                    validateStatus={errors && errors.status ? 'error' : undefined}
                    help={errors ? errors.status : null}
                    rules={[{ required: true, message: 'Please select status' }]}>
                    <Select
                        placeholder="Select"
                        filterOption={false}
                        // showSearch
                        // onChange={handleRoleChange}
                        // onSearch={handleRoleSearch}
                        name="status">

                        {status.map(item => (
                            <Option key={item.key} value={item.key}>{item.name}</Option>
                        ))}
                    </Select>
                </Form.Item>

                {!inputDisabled ?
                    <Form.Item label="Foto" name="image"
                        validateStatus={errors && errors.image ? 'error' : undefined}
                        help={errors ? errors.image : null}
                        rules={[{ required: false, message: 'Please upload image' }]}>
                        <Upload
                            name="image"
                            listType="picture-card"
                            fileList={fileList}
                            onPreview={() => setShowPreviewImage(true)}
                            onRemove={() => {
                                handleDeleteUpload()
                            }}
                            beforeUpload={(file) => {
                                const preView = URL.createObjectURL(file)
                                const image = {
                                    uid: '-1',
                                    name: 'image',
                                    status: 'done',
                                    url: preView,
                                }
                                setPreviewImage(preView)
                                setFileList([image])
                                handleUpload(file)

                                return false;
                            }}
                        >
                            {fileList.length > 0 ? null : uploadButton}
                        </Upload>

                        <Modal open={showPreviewImage}
                            footer={null} onCancel={() => setShowPreviewImage(false)}>
                            <img alt="icon" style={{ width: '100%' }} src={previewImage} />
                        </Modal>
                    </Form.Item> : null}


                <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Wajib diisi</p>
                <Divider />

                <Space>
                    <Button onClick={() => props.history.goBack()}
                        icon={<ArrowLeftOutlined />}
                    >Kembali</Button>

                    <Button onClick={() => handleReset()}
                        icon={<ReloadOutlined />}
                    >Reset</Button>

                    <Button type="primary" htmlType="submit"
                        icon={<SaveOutlined />}
                    >
                        Simpan
                    </Button>
                </Space>

            </Form>
        </Card >
    )
}

export default FormPage;